<template>
    <div>
        <p-fieldset
            :legend="PregnancyTitle"
            class="p-inputtext-sm p-input-filled"
            id="pregnancy"
        >
            <div class="p-grid">
                <div class="p-col-12 p-md-5">
                    <pregnancy-tests />
                </div>
                <div class="p-col-12 p-md-7">
                    <pregnancy-fetus-histories />
                </div>
                <div class="p-col-12 p-md-5">
                    <pregnancy-outcome-form />
                </div>
                <div class="p-col-12 p-md-7">
                    <pregnancy-live-birth-hisory />
                    <!-- <div class="p-fluid p-formgrid p-grid p-mt-3">
                        <div class="p-field p-col-12">
                            <label for="">Comments</label>
                            <p-textarea />
                        </div>
                    </div> -->

                    <!-- <p-button
                        label="save"
                        @click = "save"
                        class="p-button-success p-mt-2"
                        v-if="hasCreatePermission"
                    /> -->
                </div>
            </div>
        </p-fieldset>

        <template v-if="isBornCycle">
            <born-complication-form class="p-input-filled p-inputtext-sm" />
            <bron-pregnancy-outcome-form
                class="p-input-filled p-inputtext-sm"
            />
        </template>
    </div>
</template>

<script>
import { onMounted, ref, computed } from "vue";
import { usePregnancyInit } from "@/composable/pregnancy";
import { usePermissionCheck } from "@/composable/users";

import PregnancyTests from "@/components/pregnancy/tests.vue";
import PregnancyFetusHistories from "@/components/pregnancy/fetus-history.vue";
import PregnancyOutcomeForm from "@/components/pregnancy/outcome-form.vue";
import PregnancyLiveBirthHisory from "@/components/pregnancy/live-birth-hisory.vue";
import BornComplicationForm from "@/components/born/complicaitons-form.vue";
import BronPregnancyOutcomeForm from "@/components/born/pregnancy-outcome-form.vue";

export default {
    components: {
        PregnancyTests,
        PregnancyFetusHistories,
        PregnancyOutcomeForm,
        PregnancyLiveBirthHisory,
        BornComplicationForm,
        BronPregnancyOutcomeForm
    },
    setup() {
        const date = ref();
        const testResult = ref([]);

        const {
            store,
            toast,
            getPregnancyDetails,
            isBornCycle,
            createNewPregnancy
        } = usePregnancyInit();

        const PregnancyTitle = computed(() => {
            const fetusInput = store.state.pregnancy.fetusHistories
                ? store.state.pregnancy.fetusHistories.slice(-1)[0] || null
                : null;

            if (fetusInput) {
                const loss = fetusInput.heartBeat > 0 ? 0 : 1;
                return (
                    "Pregnancy - GS # " +
                    fetusInput.gestationalSac +
                    " HB# " +
                    fetusInput.heartBeat +
                    " Loss# " +
                    loss
                );
            } else {
                return "Pregnancy";
            }
        });

        const { hasCreatePermission } = usePermissionCheck();

        onMounted(() => {
            if (store.state.artCycle.selectedCycle) {
                getPregnancyDetails(store.state.artCycle.selectedCycle.cycleId);
            } else {
                toast.add({
                    severity: "warn",
                    summary: "",
                    detail: "Please Select Patient First",
                    life: 3000
                });
            }
            // getPregnancyDetails(150002420);

            store.dispatch(
                "born/getBronV2",
                store.state.artCycle.selectedCycle
            );
        });

        return {
            date,
            testResult,
            isBornCycle,
            hasCreatePermission,
            createNewPregnancy,
            PregnancyTitle
        };
    }
};
</script>

<style>
#pregnancy .p-component {
    font-size: 0.875rem;
}

#pregnancy .p-calendar,
.p-togglebutton {
    height: 24.5px;
}
</style>
