import { computed, inject, reactive, ref, watch } from "vue";
import { useStore } from "vuex";
import { emitter } from "@/event-bus";
import { useConfirm } from "primevue/useconfirm";
import { useToast } from "primevue/usetoast";
import { required, requiredIf } from "@vuelidate/validators";
import useVuelidate from "@vuelidate/core";
import { format } from "date-fns";
import { DATE_FORMAT_STRING } from "@/config";
import { buildPdfUrl } from "@/service/utils";
import { maxValue } from "@vuelidate/validators";

export function useInit() {
    const store = useStore();
    return { store };
}

export function usePregnancyInit() {
    const store = useStore();
    const confirm = useConfirm();
    const toast = useToast();

    const isBornCycle = computed(() => store.getters["artCycle/isBornCycle"]);

    const createNewPregnancy = () => {
        store
            .dispatch("pregnancy/createNewPregnancy", {
                cycleId: store.state.artCycle.selectedCycle.cycleId,
                patientId: store.state.artCycle.selectedCycle.patientId
            })
            .then(() => {
                toast.add({
                    severity: "success",
                    summary: "",
                    detail: "Created new successfully",
                    life: 3000
                });
                getPregnancyDetails(store.state.artCycle.selectedCycle.cycleId);
            })
            .catch(() => { })
            .finally(() => { });
    };

    const getPregnancyDetails = (cycleId) => {
        emitter.emit("app:show-page-loading", true);
        store
            .dispatch("pregnancy/getPregnancyDetails", cycleId)
            .then(() => { })
            .catch((error) => {

                createNewPregnancy();
                // confirm.require({
                //     message: "Do you want create new?",
                //     header: error.response.data.message,
                //     icon: "pi pi-exclamation-triangle",
                //     accept: () => {
                //         createNewPregnancy();
                //     },
                //     reject: () => {}
                // });
            })
            .finally(() => {
                emitter.emit("app:show-page-loading", false);


                const fetusLast = store.state.pregnancy.fetusHistories
                    ? store.state.pregnancy.fetusHistories.slice(-1)[0] || null
                    : null;
                store.commit(
                    "artCycle/SET_SELECTED_FETUSHIST",
                    fetusLast
                );
            });
    };

    return {
        store,
        toast,
        getPregnancyDetails,
        isBornCycle,
        createNewPregnancy
    };
}

export function usePregnancyTestsInit() {
    const store = useStore();
    const pregnancyTests = computed(() => store.state.pregnancy.pregnancyTests);
    const editingRows = ref([]);

    return {
        pregnancyTests,
        editingRows
    };
}

export function useFetusHistoriesInit() {
    const store = useStore();
    const editingRows = ref([]);
    const createFetusDialogDisplay = ref(false);
    const fetusHistories = computed(() => store.state.pregnancy.fetusHistories);
    const loading = ref(false);
    const confirm = useConfirm();
    const originalFetuses = ref({});


    const getFetusHistory = () => {

        loading.value = true;
        const pregnancyId = store.state.pregnancy.pregnancyDetails
            ? store.state.pregnancy.pregnancyDetails.pregnancyId
            : null;



        if (pregnancyId) {
            store
                .dispatch("pregnancy/getFetusHistory", pregnancyId)
                .then(() => {
                })
                .finally(() => {
                    loading.value = false;

                    const fetusLast = store.state.pregnancy.fetusHistories
                        ? store.state.pregnancy.fetusHistories.slice(-1)[0] || null
                        : null;
                    store.commit(
                        "artCycle/SET_SELECTED_FETUSHIST",
                        fetusLast
                    );
                });
        }
    };

    const deleteFetus = ({ fetusHistoryId }) => {
        confirm.require({
            message: "Do you want to delete this record?",
            header: "Delete Confirmation",
            icon: "pi pi-info-circle",
            acceptClass: "p-button-danger",
            accept: () => {
                loading.value = true;
                store
                    .dispatch("pregnancy/deleteFetus", fetusHistoryId)
                    .then((data) => {
                        getFetusHistory();
                    })
                    .catch((err) => {
                        console.log(err);
                    })
                    .finally(() => {
                        loading.value = false;
                    });
            },
            reject: () => { }
        });
    };

    const toast = useToast();
    const updateFetus = ({ data, index }) => {
        if (data.gestationalSac > 9 || data.heartBeat > 9) {
            toast.add({
                severity: 'error',
                summary: 'Validation Error',
                detail: 'Gestational Sac and Heartbeat values cannot exceed 9.',
                life: 4000
            });
            loading.value = false;
            return;
        }

        store.commit("pregnancy/UPDATE_FETUS", {
            index,
            data: {
                ...data,
                fetusDate: format(new Date(data.fetusDate), DATE_FORMAT_STRING)
            }
        });

        const formData = {
            ...data,
            fetusDate: format(new Date(data.fetusDate), DATE_FORMAT_STRING),
            pregnancyId: store.state.pregnancy.pregnancyDetails.pregnancyId
        };

        store.dispatch("pregnancy/updateFetus", formData).finally(() => {
            loading.value = false;
        });
    };

    const cancelUpdateFetus = ({ index }) => {
        store.commit("pregnancy/CANCEL_FETUS_UPDATE", {
            index,
            data: {
                ...originalFetuses.value[index]
            }
        });
    };

    const keepOriginalFetuses = ({ index }) => {
        originalFetuses.value[index] = Object.assign(
            {},
            fetusHistories.value[index]
        );
    };

    return {
        // fetusInput,
        fetusHistories,
        editingRows,
        createFetusDialogDisplay,
        store,
        getFetusHistory,
        loading,
        deleteFetus,
        updateFetus,
        cancelUpdateFetus,
        keepOriginalFetuses,
        toast
    };
}

export function useCreateFetusHistoryDialogIinit() {
    const store = useStore();
    const createFetusDialogDisplay = inject("createFetusDialogDisplay");
    const loading = inject("loading");

    const form = reactive({
        fetusDate: null,
        gestationalSac: null,
        heartBeat: null,
        // twins: null,
        createdBy: null
    });

    const closeModal = () => {
        createFetusDialogDisplay.value = false;
    };

    const initializeForm = () => {
        submitted.value = false;
        form.fetusDate = null;
        form.gestationalSac = null;
        form.heartBeat = null;
        // form.twins = null;
        form.createdBy = null
    };

    const rules = {
        fetusDate: { required },
        gestationalSac: { required, maxValue: maxValue(9) },
        heartBeat: { required, maxValue: maxValue(9) }
        // twins: { required, maxValue: maxValue(9) }
    };

    const v$ = useVuelidate(rules, form);
    const submitted = ref(false);

    const save = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }

        loading.value = true;

        store
            .dispatch("pregnancy/createNewFetusHistory", {
                pregnancyId: store.state.pregnancy.pregnancyDetails.pregnancyId,
                fetusDate: format(new Date(form.fetusDate), "yyyy-MM-dd"),
                gestationalSac: form.gestationalSac,
                heartBeat: form.heartBeat,
                // twins: form.twins,
                createdBy: form.createdBy
            })
            .then((data) => {
                closeModal();
            })
            .finally(() => {
                loading.value = false;
            });
    };

    return {
        createFetusDialogDisplay,
        closeModal,
        form,
        save,
        initializeForm,
        v$,
        submitted
    };
}

export function useOutcomeFormInit() {
    const store = useStore();
    const pregnancyDetails = computed(
        () => store.state.pregnancy.pregnancyDetails
    );

    const loading = ref(false);

    const form = reactive({
        isOtherFindings: false,
        otherFindings: "",
        isMiscarriage: false,
        miscarriageDate: null,
        isEctopicPregnancy: false,
        isLostToFollowup: false,
        isStillBirth: false,
        stillBirthDate: null,
        isNeonatalDeath: false,
        isPregnancyProblems: false,
        pregnancyProblems: "",
        neonatalDeathDate: null,
        comment: "",
        fetusNum: 0
    });

    const rules = computed(() => ({
        stillBirthDate: requiredIf(form.isStillBirth),
        neonatalDeathDate: requiredIf(form.isNeonatalDeath),
        miscarriageDate: requiredIf(form.isMiscarriage)
    }));

    const v$ = useVuelidate(rules, form);

    const save = () => {
        loading.value = true;
        const formData = {
            pregnancyId: store.state.pregnancy.pregnancyDetails.pregnancyId,
            cycleId: store.state.pregnancy.pregnancyDetails.cycleId,
            ...form,
            stillBirthDate: form.stillBirthDate
                ? format(new Date(form.stillBirthDate), "yyyy-MM-dd")
                : null,
            neonatalDeathDate: form.neonatalDeathDate
                ? format(new Date(form.neonatalDeathDate), "yyyy-MM-dd")
                : null,
            miscarriageDate: form.miscarriageDate
                ? format(new Date(form.miscarriageDate), "yyyy-MM-dd")
                : null
        };
        store
            .dispatch("pregnancy/savePregnancyOutcome", formData)
            .then(() => { })
            .catch(() => { })
            .finally(() => {
                loading.value = false;
            });
    };

    const deletePregnancy = () => {
        loading.value = true;
        store
            .dispatch("pregnancy/deletePregnancy")
            .then(() => {
                location.reload();
            })
            .finally(() => {
                loading.value = false;
            });
    };

    const openPdfReport = () => {
        const params = {
            cycleId: store.state.artCycle.selectedCycle.cycleId
        };

        let url = buildPdfUrl("PregnancyReport", params);
        window.open(url);
    };

    return {
        store,
        pregnancyDetails,
        form,
        save,
        loading,
        deletePregnancy,
        openPdfReport
    };
}

export function useLiveBirthInit() {
    const confirm = useConfirm();
    const store = useStore();
    const loading = ref(false);
    const editingRows = ref([]);
    const liveBirthHisotory = computed(
        () => store.state.pregnancy.liveBirthHisotory
    );
    const createLiveBrithDisplay = ref(false);

    const sexOptions = ref(["Male", "Female"]);
    const unitOptions = ref(["lbs", "kg"]);
    const deliveryMethodOptions = ref(["Caesarean", "Normal"]);

    const oepnCreateLiveBirthDialog = () => {
        createLiveBrithDisplay.value = true;
    };

    const getLiveBirthHistory = () => {
        loading.value = true;
        const pregnancyId = store.state.pregnancy.pregnancyDetails
            ? store.state.pregnancy.pregnancyDetails.pregnancyId
            : null;

        if (pregnancyId) {
            store
                .dispatch("pregnancy/getLiveBirthHistory", pregnancyId)
                .then(() => { })
                .catch(() => { })
                .finally(() => {
                    loading.value = false;
                });
        }
    };

    const deleteLiveBirth = ({ liveBirthHistoryId }) => {
        confirm.require({
            message: "Do you want to delete this record?",
            header: "Delete Confirmation",
            icon: "pi pi-info-circle",
            acceptClass: "p-button-danger",
            accept: () => {
                loading.value = true;
                store
                    .dispatch(
                        "pregnancy/deletLiveBirthHistory",
                        liveBirthHistoryId
                    )
                    .then((data) => {
                        getLiveBirthHistory();
                    })
                    .catch((err) => { })
                    .finally(() => {
                        loading.value = false;
                    });
            },
            reject: () => { }
        });
    };

    const originalLiveBirth = ref({});
    const keepOriginalLiveBirth = ({ index }) => {
        originalLiveBirth.value[index] = Object.assign(
            {},
            liveBirthHisotory.value[index]
        );
    };

    const cancelUpdateLiveBirth = ({ index }) => {
        store.commit("pregnancy/CANCEL_LIVE_BIRTH_UPDATE", {
            index,
            data: {
                ...originalLiveBirth.value[index]
            }
        });
    };

    const updateLiveBirth = ({ data }) => {
        store.dispatch("pregnancy/updateLiveBirth", { ...data });
    };

    return {
        liveBirthHisotory,
        createLiveBrithDisplay,
        oepnCreateLiveBirthDialog,
        loading,
        getLiveBirthHistory,
        deleteLiveBirth,
        editingRows,
        sexOptions,
        unitOptions,
        deliveryMethodOptions,
        keepOriginalLiveBirth,
        cancelUpdateLiveBirth,
        updateLiveBirth
    };
}

export function useLiveBirthDialogInit() {
    const store = useStore();
    const initializeForm = () => {
        submitted.value = false;
        form.sex = null;
        form.weight = null;
        form.unit = null;
        form.birthDate = null;
        form.deliveryMethod = null;
        form.placeOfBirth = null;
    };
    const createLiveBrithDisplay = inject("createLiveBrithDisplay");
    const form = reactive({
        sex: null,
        weight: null,
        unit: null,
        birthDate: null,
        deliveryMethod: null,
        placeOfBirth: null
    });
    const rules = {
        sex: { required },
        birthDate: { required }
    };
    const submitted = ref(false);
    const v$ = useVuelidate(rules, form);

    const {
        sexOptions,
        unitOptions,
        deliveryMethodOptions
    } = useLiveBirthInit();

    const closeCreateLiveBirthDialog = () => {
        createLiveBrithDisplay.value = false;
    };

    const { getLiveBirthHistory } = useLiveBirthInit();

    const loading = inject("loading", loading);

    const save = () => {
        submitted.value = true;
        if (v$.value.$invalid) {
            return;
        }

        loading.value = true;

        const formData = {
            ...form,
            birthDate: format(new Date(form.birthDate), DATE_FORMAT_STRING),
            pregnancyId: store.state.pregnancy.pregnancyDetails.pregnancyId
        };

        store
            .dispatch("pregnancy/createLiveBirth", formData)
            .then((data) => {
                // getLiveBirthHistory();
                closeCreateLiveBirthDialog();
            })
            .catch((err) => {
                console.log(err);
            })
            .finally(() => {
                loading.value = false;
            });
    };

    return {
        initializeForm,
        createLiveBrithDisplay,
        form,
        closeCreateLiveBirthDialog,
        save,
        sexOptions,
        unitOptions,
        deliveryMethodOptions,
        v$,
        submitted
    };
}
