<template>
    <p-panel header="Delivery / Live Birth Hisotory">
        <p-data-table
            :value="liveBirthHisotory"
            datakey="liveBirthHistoryId"
            editMode="row"
            v-model:editingRows="editingRows"
            showGridlines
            class="p-datatable-sm p-mt-1 p-mb-2"
            :loading="loading"
            @row-edit-init="keepOriginalLiveBirth"
            @row-edit-cancel="cancelUpdateLiveBirth"
            @row-edit-save="updateLiveBirth"
        >
            <p-column header="Sex" field="sex">
                <template #editor="{data}">
                    <p-dropdown v-model="data.sex" :options="sexOptions" />
                </template>
            </p-column>
            <p-column header="Weight" field="weight">
                <template #editor="{data}">
                    <p-input-text v-model="data.weight" />
                </template>
            </p-column>
            <p-column header="Unit" field="unit">
                <template #editor="{data}">
                    <p-dropdown v-model="data.unit" :options="unitOptions" />
                </template>
            </p-column>
            <p-column header="Birth Date" field="birthDate" />
            <p-column header="Delivery Method" field="deliveryMethod">
                <template #editor="{data}">
                    <p-dropdown
                        v-model="data.deliveryMethod"
                        :options="deliveryMethodOptions"
                    />
                </template>
            </p-column>
            <p-column header="Place of Birth" field="placeOfBirth">
                <template #editor="{data}">
                    <p-input-text v-model="data.placeOfBirth" />
                </template>
            </p-column>
            <p-column
                header="Del"
                style="width: 40px;"
                bodyStyle="text-align: center;"
            >
                <template #body="{data}">
                    <p-button
                        icon="pi pi-trash"
                        class="p-button-rounded p-button-danger p-button-text"
                        @click="deleteLiveBirth(data)"
                    />
                </template>
            </p-column>
            <p-column
                :rowEditor="true"
                style="width: 10%; min-width:8rem; color: green"
                bodyStyle="text-align:center;"
            >
            </p-column>

            <template #footer>
                <p-button
                    label="Create"
                    class="p-button-success p-mr-2"
                    @click="oepnCreateLiveBirthDialog"
                />
            </template>
        </p-data-table>
    </p-panel>

    <pregnancy-create-live-birth-dialog />
</template>

<script>
import { useLiveBirthInit } from "@/composable/pregnancy";
import { provide } from "vue";

import PregnancyCreateLiveBirthDialog from "@/components/pregnancy/create-live-birth-dialog.vue";

export default {
    components: { PregnancyCreateLiveBirthDialog },
    setup() {
        const {
            liveBirthHisotory,
            createLiveBrithDisplay,
            oepnCreateLiveBirthDialog,
            loading,
            deleteLiveBirth,
            editingRows,
            sexOptions,
            unitOptions,
            deliveryMethodOptions,
            keepOriginalLiveBirth,
            cancelUpdateLiveBirth,
            updateLiveBirth
        } = useLiveBirthInit();

        provide("createLiveBrithDisplay", createLiveBrithDisplay);
        provide("loading", loading);

        return {
            liveBirthHisotory,
            oepnCreateLiveBirthDialog,
            loading,
            deleteLiveBirth,
            editingRows,
            sexOptions,
            unitOptions,
            deliveryMethodOptions,
            keepOriginalLiveBirth,
            cancelUpdateLiveBirth,
            updateLiveBirth
        };
    }
};
</script>
