<template>
    <p-block-ui :blocked="loading">
        <p-panel header="Pregnancy Outcome of current cycle">
            <p-progress-bar
                mode="indeterminate"
                style="height: .5em"
                v-if="loading"
            />

            <div class="p-fluid p-formgrid p-grid">
                <!-- <div class="p-field p-col-12 p-md-2">
                    <label># Fetus</label>
                </div>
                <div class="p-field p-col-12 p-md-10 p-mb-1">
                    <p-input-text v-model="form.fetusNum" type="number" min="0" style="width: 200px;"/>
                </div> -->
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox
                        v-model="form.isEctopicPregnancy"
                        :binary="true"
                    />
                    <label>Ectopic Pregnancy </label>
                </div>
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox
                        v-model="form.isLostToFollowup"
                        :binary="true"
                    />
                    <label>Lost To FollowUp </label>
                </div>
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox
                        v-model="form.isStillBirth"
                        :binary="true"
                        @change="changeDate('sti')"
                    />
                    <label>Still Birth </label>
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <p-calendar
                        v-model="form.stillBirthDate"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                    />
                </div>
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox
                        v-model="form.isNeonatalDeath"
                        :binary="true"
                        @change="changeDate('neo')"
                    />
                    <label>Neonathal Birth </label>
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <p-calendar
                        v-model="form.neonatalDeathDate"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                    />
                </div>
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox
                        v-model="form.isMiscarriage"
                        :binary="true"
                        @change="changeDate('mis')"
                    />
                    <label for="">Miscarriage</label>
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <p-calendar
                        v-model="form.miscarriageDate"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                    />
                </div>
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox
                        v-model="form.isPregnancyProblems"
                        :binary="true"
                    />
                    <label for="">Pregnancy Problem</label>
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <p-textarea v-model="form.pregnancyProblems" />
                </div>
                <div class="p-field-checkbox p-col-12 p-md-6">
                    <p-checkbox v-model="form.isOtherFindings" :binary="true" />
                    <label for="">Other Findings</label>
                </div>
                <div class="p-field p-col-12 p-md-6">
                    <p-textarea v-model="form.otherFindings" />
                </div>
                <div class="p-field p-col-12 p-md-12 p-mb-2">
                    <label class="p-mr-2">Comments</label>
                    <p-textarea v-model="form.comment" />
                </div>
            </div>
            <p-button
                label="Save"
                class="p-mr-2 p-button-success"
                @click="save"
                v-if="hasCreatePermission || hasUpdatePermission"
            />
            <p-button
                label="Delete"
                class="p-mr-2 p-button-danger"
                @click="deletePregnancy"
                v-if="hasDeletePermission"
            />
            <p-button
                label="Report"
                class="p-mr-2 p-button-secondary"
                @click="openPdfReport"
            />
        </p-panel>
    </p-block-ui>
</template>

<script>
import { useOutcomeFormInit } from "@/composable/pregnancy";
import { ref, watch } from "vue";
import { usePermissionCheck } from "@/composable/users";
export default {
    setup() {
        const {
            store,
            pregnancyDetails,
            form,
            save,
            loading,
            deletePregnancy,
            openPdfReport
        } = useOutcomeFormInit();

        const {
            hasCreatePermission,
            hasUpdatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        watch(pregnancyDetails, (newValue) => {
            form.isOtherFindings = newValue.isOtherFindings;
            form.otherFindings = newValue.otherFindings;
            form.isMiscarriage = newValue.isMiscarriage;
            if (newValue.miscarriageDate != null)
                form.miscarriageDate = new Date(newValue.miscarriageDate);
            form.isEctopicPregnancy = newValue.isEctopicPregnancy;
            form.isLostToFollowup = newValue.isLostToFollowup;
            form.isStillBirth = newValue.isStillBirth;
            if (newValue.stillBirthDate != null)
                form.stillBirthDate = new Date(newValue.stillBirthDate);
            form.isNeonatalDeath = newValue.isNeonatalDeath;
            form.isPregnancyProblems = newValue.isPregnancyProblems;
            form.pregnancyProblems = newValue.pregnancyProblems;
            if (newValue.neonatalDeathDate != null)
                form.neonatalDeathDate = new Date(newValue.neonatalDeathDate);
            form.comment = newValue.comment;
            form.fetusNum = newValue.fetusNum;
        });

        const changeDate = (paramname) => {
            if (paramname == "mis") {
                if (form.isMiscarriage) form.miscarriageDate = new Date();
                else form.miscarriageDate = null;
            } else if (paramname == "sti") {
                if (form.isStillBirth) form.stillBirthDate = new Date();
                else form.stillBirthDate = null;
            } else if (paramname == "neo") {
                if (form.isNeonatalDeath) form.neonatalDeathDate = new Date();
                else form.neonatalDeathDate = null;
            }
        };
        // watch(form, (newValue) => {
        //     if (newValue.isMiscarriage) form.miscarriageDate = new Date();
        //     else form.miscarriageDate = null;

        //     if (newValue.isStillBirth) form.stillBirthDate = new Date();
        //     else form.stillBirthDate = null;

        //     if (newValue.isNeonatalDeath) form.neonatalDeathDate = new Date();
        //     else form.neonatalDeathDate = null;
        // });

        return {
            form,
            save,
            loading,
            deletePregnancy,
            openPdfReport,
            hasCreatePermission,
            hasUpdatePermission,
            hasDeletePermission,
            changeDate
        };
    }
};
</script>

<style scoped>
.p-progressbar {
    margin: -14px;
    margin-bottom: 10px;
}
</style>
