<template>
    <p-card class="p-mt-2">
        <template #content>
            <p-divider align="left" class="p-mt-0">
                <b>Pregnancy Outcome</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-mt-2">
                    <label for="">Pregnancy / Birth outcome</label>
                </div>
                <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.pregnancyBirthOutcome"
                        :options="pregnancyBirthOutcomeOptions"
                        optionLabel="label"
                        optionValue="value"
                        :showClear="true"
                    />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Newborn date of birth</label>
                </div>
                <div class="p-col-fixed">
                    <p-calendar
                        v-model="form.newbornDateBirth"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': v$.newbornDateBirth.$invalid
                        }"
                    />
                </div>
                <i
                    v-if="v$.newbornDateBirth.validator.$invalid"
                    class="p-col-fixed pi pi-info-circle p-ml-2 p-mt-2"
                    v-tooltip.right="v$.newbornDateBirth.validator.$message"
                    style="color: #f44336"
                ></i>
                <i
                    v-if="v$.newbornDateBirth.validDate.$invalid"
                    class="p-col-fixed pi pi-info-circle p-ml-2 p-mt-2"
                    v-tooltip.right="v$.newbornDateBirth.validDate.$message"
                    style="color: #f44336"
                ></i>

                <div class="p-col-fixed p-mt-2">
                    <label for="">Pregnancy Loss Date</label>
                </div>
                <div class="p-col-fixed">
                    <p-calendar
                        v-model="form.pregnancyLossDate"
                        :showIcon="true"
                        dateFormat="yy-mm-dd"
                        :class="{
                            'p-invalid': v$.pregnancyLossDate.$invalid
                        }"
                    />
                </div>
                <i
                    v-if="v$.pregnancyLossDate.validator.$invalid"
                    class="p-col-fixed pi pi-info-circle p-ml-2 p-mt-2"
                    v-tooltip.right="v$.pregnancyLossDate.validator.$message"
                    style="color: #f44336"
                ></i>
                <i
                    v-if="v$.pregnancyLossDate.validDate.$invalid"
                    class="p-col-fixed pi pi-info-circle p-ml-2 p-mt-2"
                    v-tooltip.right="v$.pregnancyLossDate.validDate.$message"
                    style="color: #f44336"
                ></i>
            </div>

            <p-divider align="left" class="p-mt-0">
                <b>Babies Born</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-mt-2">
                    <label for=""># babies born</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-number
                        v-model="form.babiesborn"
                        :class="{
                            'p-invalid': v$.babiesborn.$invalid
                        }"
                        :max="99"
                        style="width: 50px"
                    />
                </div>
                <!-- <div class="p-col-fixed p-mt-2">
                    <label for="">Gestational Age</label>
                </div>
                <i
                    v-if="v$.babiesborn.$invalid"
                    class="p-col-fixed pi pi-info-circle p-ml-2 p-mt-2"
                    v-tooltip.right="v$.babiesborn.validator.$message"
                    style="color: #f44336"
                ></i>

                <div class="p-col-fixed">
                    <p-input-text
                        v-model="form.gestationalWeeks"
                        style="width: 50px"
                        type="number"
                    />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Weeks</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-text
                        v-model="form.gestationalDays"
                        style="width: 50px"
                        type="number"
                    />
                </div>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Days</label>
                </div> -->
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
                :disabled="v$.$invalid"
            />

            <p-button
                icon="pi pi-check"
                label="Mark As Completed"
                @click="markAsComplete('pregnancy')"
                class="p-ml-2"
            />
        </template>
    </p-card>
</template>

<script>
import {
    useBornMarkAsComplete,
    usePregnancyOutcomeForm
} from "@/composable/born";
import { inject, onMounted, onUnmounted, toRefs, watch } from "vue-demi";
import { emitter } from "@/event-bus";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            v$,
            fillForm,
            save,
            loading,
            pregnancyBirthOutcomeOptions
        } = usePregnancyOutcomeForm();

        watch(() => store.state.born.pregnancyOutcome, fillForm);

        const { isInBornPage } = toRefs(props);
        const bornForm = inject("bornForm", null);

        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.pregnancyOutcome = { ...newValue };
        });

        watch(
            () => form.babiesborn,
            (newVal) => {
                store.commit("born/SET_NO_BABIES_BORN", newVal);
            }
        );

        onMounted(() => {
            emitter.on("born:save-temp", save);

            if (isInBornPage.value) {
                return;
            }

            if (store.state.born.pregnancyOutcome == null) {
                store.dispatch(
                    "born/getBronV2",
                    store.state.artCycle.selectedCycle
                );
            } else {
                fillForm(store.state.born.pregnancyOutcome);
            }
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        const { markAsComplete } = useBornMarkAsComplete();

        return {
            isInBornPage,
            form,
            v$,
            pregnancyBirthOutcomeOptions,
            save,
            loading,
            markAsComplete
        };
    }
};
</script>

<style scoped>
.p-calendar {
    height: 24.5px;
}
</style>
