<template>
    <p-card class="p-mt-2">
        <template #content>
            <p-divider align="left" class="p-mt-0">
                <b>Complications</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-mt-2">
                    <label for="">Complications</label>
                </div>
                <div class="p-col-fixed">
                    <p-multi-select
                        v-model="form.complications"
                        :options="complicationOptions"
                        optionLabel="label"
                        optionValue="value"
                        style="width: 300px"
                    />
                </div>
                <i
                    v-if="v$.complications.$invalid"
                    class="p-col-fixed pi pi-info-circle p-ml-2 p-mt-2"
                    v-tooltip.right="v$.complications.required.$message"
                    style="color: #f44336"
                ></i>
                <div class="p-col-fixed p-mt-2">
                    <label for="">Hospitalization</label>
                </div>
                <div class="p-col-fixed p-mt-1">
                    <div class="p-formgroup-inline">
                        <div class="p-field-checkbox">
                            <p-radio-button
                                name="hospitalization"
                                value="Y"
                                v-model="form.spermTooLowCount"
                                :class="{
                                    'p-invalid': v$.spermTooLowCount.$invalid
                                }"
                            />
                            <label>Yes</label>
                        </div>
                        <div class="p-field-checkbox">
                            <p-radio-button
                                name="hospitalization"
                                value="N"
                                v-model="form.spermTooLowCount"
                                :class="{
                                    'p-invalid': v$.spermTooLowCount.$invalid
                                }"
                            />
                            <label>NO</label>
                        </div>
                    </div>
                </div>
            </div>

            <p-divider align="left" class="p-mt-0">
                <b>Treatment outcome</b>
            </p-divider>

            <div class="p-grid p-fluid">
                <div class="p-col-fixed p-mt-2">
                    <label for="">Treatment Outcome</label>
                </div>

                <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.treatmentOutcome"
                        :options="treatmentOutcomeOptions"
                        optionLabel="label"
                        optionValue="value"
                    />
                </div>

                <!-- <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.treatmentOutcome"
                        :options="treatmentOutcomeOptions"
                        optionLabel="label"
                        optionValue="value"
                        :class="{
                            'p-invalid': v$.treatmentOutcome.$invalid
                        }"
                    />
                </div>
                <i
                    v-if="v$.treatmentOutcome.$invalid"
                    class="p-col-fixed pi pi-info-circle p-mt-2"
                    v-tooltip.right="v$.treatmentOutcome.validator.$message"
                    style="color: #f44336"
                ></i> -->

                <div class="p-col-fixed p-mt-2">
                    <label for="">Type of Pregnancy</label>
                </div>
                <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.typeOfPregnancy"
                        :options="typeOfPregnancyOptions"
                        optionLabel="label"
                        optionValue="value"
                        :class="{
                            'p-invalid': v$.typeOfPregnancy.$invalid
                        }"
                    />
                </div>
                <i
                    v-if="v$.typeOfPregnancy.$invalid"
                    class="p-col-fixed pi pi-info-circle p-mt-2"
                    v-tooltip.right="v$.typeOfPregnancy.validator.$message"
                    style="color: #f44336"
                ></i>
                <div class="p-col-fixed p-mt-2">
                    <label for=""># US fetal sacs</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-number
                        v-model="form.fetalSacsUS"
                        :class="{
                            'p-invalid': v$.fetalSacsUS.$invalid
                        }"
                        :max="99"
                    />
                </div>
                <i
                    v-if="v$.fetalSacsUS.$invalid"
                    class="p-col-fixed pi pi-info-circle p-mt-2"
                    v-tooltip.right="v$.fetalSacsUS.validator.$message"
                    style="color: #f44336"
                ></i>

                <div class="p-col-fixed p-mt-2">
                    <label for="">#US fetal hearts</label>
                </div>
                <div class="p-col-fixed">
                    <p-input-number
                        v-model="form.fetalheartsUS"
                        :class="{
                            'p-invalid': v$.fetalheartsUS.$invalid
                        }"
                        :max="99"
                    />
                </div>
                <i
                    v-if="v$.fetalheartsUS.$invalid"
                    class="p-col-fixed pi pi-info-circle p-mt-2"
                    v-tooltip.right="v$.fetalheartsUS.validator.$message"
                    style="color: #f44336"
                ></i>

                <div class="p-col-fixed p-mt-2">
                    <label for="">Chorionicity</label>
                </div>
                <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.chorionicity"
                        :options="chorionicityOptions"
                        optionLabel="label"
                        optionValue="value"
                        :class="{
                            'p-invalid': v$.chorionicity.$invalid
                        }"
                    />
                </div>
                <i
                    v-if="v$.chorionicity.$invalid"
                    class="p-col-fixed pi pi-info-circle p-mt-2"
                    v-tooltip.right="v$.chorionicity.validator.$message"
                    style="color: #f44336"
                ></i>

                <div class="p-col-fixed p-mt-2">
                    <label for="">Elective fetal reduction</label>
                </div>
                <div class="p-col-fixed">
                    <p-dropdown
                        v-model="form.electiveFetalReduction"
                        :options="electiveFetalReductionOptions"
                        optionLabel="label"
                        optionValue="value"
                        :class="{
                            'p-invalid': v$.electiveFetalReduction.$invalid
                        }"
                    />
                </div>
                <i
                    v-if="v$.electiveFetalReduction.$invalid"
                    class="p-col-fixed pi pi-info-circle p-mt-2"
                    v-tooltip.right="
                        v$.electiveFetalReduction.validator.$message
                    "
                    style="color: #f44336"
                ></i>
            </div>
        </template>

        <template #footer v-if="!isInBornPage">
            <p-button
                icon="pi pi-save"
                label="Save"
                @click="save"
                :loading="loading"
                :disabled="v$.$invalid"
            />
        </template>
    </p-card>
</template>

<script>
import { useComplicationFormInit } from "@/composable/born";
import { inject, onMounted, onUnmounted, ref, toRefs, watch } from "vue-demi";
import { emitter } from "@/event-bus";

export default {
    props: {
        isInBornPage: {
            type: Boolean,
            default: false
        }
    },
    setup(props) {
        const {
            store,
            form,
            v$,
            save,
            fillForm,
            loading,
            confirm,
            complicationOptions,
            treatmentOutcomeOptions,
            typeOfPregnancyOptions,
            chorionicityOptions,
            electiveFetalReductionOptions
        } = useComplicationFormInit();

        const fetalSacsUSWarning = ref(false);

        const fillFieldsByEmbryoTransfer = (embryoTransfer) => {
            if (embryoTransfer == "no") {
                form.typeOfPregnancy = "Not Pregnant";
                form.treatmentOutcome = "Cycle not completed to transfer";
            } else {
                form.typeOfPregnancy = null;
                form.treatmentOutcome = null;
            }
        };

        watch(
            () => store.state.born.dependentOn.embryoTransfer,
            (newVal) => {
                fillFieldsByEmbryoTransfer(newVal);
            }
        );

        watch(
            [() => form.typeOfPregnancy, () => form.fetalSacsUS],
            ([typeOfPregnancy, fetalSacsUS]) => {
                if (
                    typeOfPregnancy == "Ectopic" &&
                    parseInt(fetalSacsUS) == 0
                ) {
                    confirm.require({
                        message:
                            "Please confirm with physician that ectopic pregnancy as # of US fetal sacs = 0",
                        header: "Confirmation",
                        icon: "pi pi-exclamation-triangle",
                        accept: () => {},
                        reject: () => {}
                    });
                }
            }
        );

        watch(
            () => form.spermTooLowCount,
            (newVal) => {
                if (newVal == "N") {
                    form.complications = ["NONE"];
                } else {
                    form.complications = [];
                }
            }
        );

        watch(() => store.state.born.complications, fillForm);

        const { isInBornPage } = toRefs(props);
        const bornForm = inject("bornForm", null);

        watch(form, (newValue) => {
            if (bornForm == null) return;
            bornForm.complications = { ...newValue };
        });

        onMounted(() => {
            emitter.on("born:save-temp", save);

            fillFieldsByEmbryoTransfer(
                store.state.born.dependentOn.embryoTransfer
            );

            if (isInBornPage.value) {
                return;
            }

            if (store.state.born.complications == null) {
                store.dispatch(
                    "born/getBronV2",
                    store.state.artCycle.selectedCycle
                );
            } else {
                fillForm(store.state.born.complications);
            }
        });

        onUnmounted(() => {
            emitter.off("born:save-temp", save);
        });

        return {
            isInBornPage,
            form,
            v$,
            save,
            loading,
            complicationOptions,
            treatmentOutcomeOptions,
            typeOfPregnancyOptions,
            chorionicityOptions,
            electiveFetalReductionOptions,
            fetalSacsUSWarning
        };
    }
};
</script>
