<template>
    <p-panel header="Test Results">
        <p-data-table
            :value="pregnancyTests"
            datakey="btId"
            editMode="row"
            v-model:editingRows="editingRows"
            responsiveLayout="scroll"
            showGridlines
            class="p-datatable-sm p-mt-1 p-mb-2"
        >
            <p-column header="Date" field="btDate">
                <template #editor="{data}">
                    <p-input-text v-model="data.btDate" />
                </template>
            </p-column>
            <p-column header="BhCG" field="bhcg">
                <template #editor="{data}">
                    <p-input-text v-model="data.bhcg" />
                </template>
            </p-column>
            <p-column
                :rowEditor="true"
                style="width: 10%; min-width:8rem"
                bodyStyle="text-align:center"
            ></p-column>
        </p-data-table>
    </p-panel>
</template>

<script>
import { usePregnancyTestsInit } from "@/composable/pregnancy";

export default {
    setup() {
        const { pregnancyTests, editingRows } = usePregnancyTestsInit();
        return { pregnancyTests, editingRows };
    }
};
</script>
