<template>
    <p-dialog
        header="New Delivery"
        v-model:visible="createLiveBrithDisplay"
        :style="{ width: '400px' }"
        :modal="true"
        class="p-inputtext-sm"
        @show="initializeForm"
    >
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:140px">
                Sex
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="form.sex"
                    :options="sexOptions"
                    :class="{
                        'p-invalid': submitted && v$.sex.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.sex.$invalid && submitted) ||
                            v$.sex.$pending.$response
                    "
                    class="p-error"
                    >{{ v$.sex.required.$message.replace("Value", "Sex") }}
                </small>
            </div>
        </div>
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:140px">Weight</label>
            <div class="p-col">
                <p-input-text v-model="form.weight" />
            </div>
        </div>
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:140px">
                Unit
            </label>
            <div class="p-col">
                <p-dropdown v-model="form.unit" :options="unitOptions" />
            </div>
        </div>
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:140px">Date of Birth</label>
            <div class="p-col">
                <p-calendar
                    v-model="form.birthDate"
                    dateFormat="yy-mm-dd"
                    :showIcon="true"
                    :class="{
                        'p-invalid': submitted && v$.birthDate.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.birthDate.$invalid && submitted) ||
                            v$.birthDate.$pending.$response
                    "
                    class="p-error"
                    >{{
                        v$.birthDate.required.$message.replace(
                            "Value",
                            "Birth Date"
                        )
                    }}
                </small>
            </div>
        </div>
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:140px">
                Delivery Methiod
            </label>
            <div class="p-col">
                <p-dropdown
                    v-model="form.deliveryMethod"
                    :options="deliveryMethodOptions"
                    :filter="true"
                />
            </div>
        </div>
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:140px">
                Place of Brith
            </label>
            <div class="p-col">
                <p-input-text v-model="form.placeOfBirth" />
            </div>
        </div>

        <template #footer>
            <p-button
                label="No"
                icon="pi pi-times"
                @click="closeCreateLiveBirthDialog"
                class="p-button-text"
            />
            <p-button
                label="Save"
                icon="pi pi-save"
                class="p-button-success"
                @click="save"
                autofocus
            />
        </template>
    </p-dialog>
</template>

<script>
import { useLiveBirthDialogInit } from "@/composable/pregnancy";
export default {
    setup() {
        const {
            createLiveBrithDisplay,
            initializeForm,
            form,
            closeCreateLiveBirthDialog,
            save,
            sexOptions,
            unitOptions,
            deliveryMethodOptions,
            v$,
            submitted
        } = useLiveBirthDialogInit();

        return {
            createLiveBrithDisplay,
            initializeForm,
            form,
            closeCreateLiveBirthDialog,
            save,
            sexOptions,
            unitOptions,
            deliveryMethodOptions,
            v$,
            submitted
        };
    }
};
</script>
