<template>
    <p-dialog
        header="Create New Fetus"
        v-model:visible="createFetusDialogDisplay"
        :style="{ width: '400px' }"
        :modal="true"
        class="p-inputtext-sm"
        @show="initializeForm"
    >
        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:100px">Date</label>
            <div class="p-col">
                <p-calendar
                    v-model="form.fetusDate"
                    dateFormat="yy-mm-dd"
                    :showIcon="true"
                    :class="{
                        'p-invalid': submitted && v$.fetusDate.$invalid
                    }"
                />
                <small
                    v-if="
                        (v$.fetusDate.$invalid && submitted) ||
                            v$.fetusDate.$pending.$response
                    "
                    class="p-error"
                    >{{
                        v$.fetusDate.required.$message.replace(
                            "Value",
                            "Fetus Date"
                        )
                    }}
                </small>
            </div>
        </div>
        <div class="p-field p-grid">
        <label class="p-col-fixed" style="width:100px">Gestational Sac</label>
            <div class="p-col">
                <p-input-number
                    v-model="form.gestationalSac"
                    :class="{ 'p-invalid': submitted && v$.gestationalSac.$invalid }"
                />
                <small v-if="v$.gestationalSac.$invalid && submitted" class="p-error p-d-block">
                    Gestational Sac must be between 0 and 9.
                </small>
            </div>
        </div>

        <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:100px"># Heart Beat</label>
            <div class="p-col">
                <p-input-number
                    v-model="form.heartBeat"
                    :class="{ 'p-invalid': submitted && v$.heartBeat.$invalid }"
                />
                <small v-if="v$.heartBeat.$invalid && submitted" class="p-error p-d-block">
                    Heart Beat must be between 0 and 9.
                </small>
            </div>
        </div>

        <!-- <div class="p-field p-grid">
            <label class="p-col-fixed" style="width:100px">Twins</label>
            <div class="p-col">
                <p-input-number
                    v-model="form.twins"
                    :class="{ 'p-invalid': submitted && v$.twins.$invalid }"
                />
                <small v-if="v$.twins.$invalid && submitted" class="p-error p-d-block">
                    Twins must be between 0 and 9.
                </small>
            </div>
        </div> -->

        <template #footer>
            <p-button
                label="No"
                icon="pi pi-times"
                @click="closeModal"
                class="p-button-text"
            />
            <p-button
                label="Create"
                icon="pi pi-save"
                class="p-button-success"
                @click="save"
                autofocus
            />
        </template>
    </p-dialog>
</template>

<script>
import { useCreateFetusHistoryDialogIinit } from "@/composable/pregnancy";
export default {
    setup() {
        const {
            createFetusDialogDisplay,
            closeModal,
            save,
            form,
            initializeForm,
            v$,
            submitted
        } = useCreateFetusHistoryDialogIinit();

        return {
            createFetusDialogDisplay,
            closeModal,
            form,
            initializeForm,
            save,
            v$,
            submitted
        };
    }
};
</script>
